.nav-gw4 {display:block !important; margin-top:@line-height-base/2;}

.hiddenStructure { display: none !important;}

.formTabs {
    &:extend(.nav);
    &:extend(.nav-pills);
}

// Revisar - Posat a ull
#ajax-spinner {
    width: 20px;
    height: 20px;
    position: fixed;
    background-position: center center;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    margin-left: -10px;
}




.link-parent {
    //icon-chevron-left
    &:extend(.btn);
    &:extend(.btn-info);
    &:extend(.btn-small);
    font-weight: bold;
    margin-bottom: 1em;
    &:before {
        background: url("/++gw++static/images/gh-w.png") no-repeat -290px -95px;
        content: " ";
        display: inline-block;
        height: 14px;
        margin-right: 3px;
        position: relative;
        top: 2px;
        width: 14px;
    }
}
#category.documentByLine {&:extend(.muted);&:extend(.box);&:extend(.box-small);
    &:before { content:""; &:extend([class^="icon-"]); &:extend(.icon-tags); width:15px;}
}




