.actionSeparator {border-top:1px solid #b2b2b2; margin-top: 5px;
    & > a {
        margin-top:5px;
    }
}
/* aixo no serveix per res pero mira...*/
#actionMenuContent #more {}

// taules i estats fet maco al folrder_contents
#listing-table tr td:nth-child(6) .state-restricted-to-managers {&:extend(.label); &:extend(.label-inverse);}
#listing-table tr td:nth-child(6) .state-visible {&:extend(.label); &:extend(.label-success);}
#listing-table tr td:nth-child(6) .state-intranet {&:extend(.label); &:extend(.label-intranet);}
#listing-table tr td:nth-child(6) .state-private {&:extend(.label); &:extend(.label-important);}



//FITXERS
.thumb-img img {
    &:extend(.thumbnail);
}


// VISTES
.tileItem img {&:extend(.img-polaroid); float:right; margin-top: 1em;}

.tileHeadline { &:extend(.xxl); &:extend(.liniaDalt);}
.tileFooter:before { content:"";  &:extend([class^="icon-"]); &:extend(.icon-plus-sign);}

.template-atct_album_view #content-core {&:extend(.row-fluid);}
.photoAlbumEntry {&:extend(.col-md-3);}
.photoAlbumEntry a {&:extend(.thumbnail);
    & img {width:100%;}
    & .photoAlbumEntryTitle {
        &:extend(.caption); }
}




// SELECTS
.plone_jscalendar > * {display: inline-block;}
.plone_jscalendar select {width:auto; display: inline-block;}




.pb-ajax {
  &:extend(.modal);}

.pb-ajax .documentFirstHeading, .pb-ajax > div > h2 {
  &:extend(.modal-header); margin:0; font-size: 130.769% !important;
}

.pb-ajax #content-core, .pb-ajax .documentDescription, .pb-ajax #content-history {
  &:extend(.modal-body); margin-bottom:0;
  max-height: 400px;
}
.pb-ajax .historyRecord {border-bottom: 1px solid #ccc;margin-bottom: 10px;padding-bottom: 10px;}

input[type="checkbox"].required:after,
input[type="radio"].required:after { content: none; }


.recaptchatable {background: #fff;}

.fieldUploadFile {display: none; }
.fieldTextFormat {display: none; }


