.portaltype-packet .estudis {
	& *.gris {&:extend(.muted);}
	caption {display: none;}
	table.minimal {&:extend(.table); &:extend(.table-condensed); &:extend(.table-striped);}
	td:first-child, th:first-child {min-width:150px;}
	.note {&:extend(.lead-small);}
	.right {float: right;}
	dl.fitxa {&:extend(.dl-horizontal); &:extend(.margin0);}
	.caixa3 {&:extend(.box); &:extend(.box-small); &:extend(.visualClear);}
	.llistatEnllacos {&:extend(.list-links); ; &:extend(.list-condensed);}

}



// estil copiat desde style.css

.tab-content .unit-content header h1 {
    font-size: 17px;
    line-height: 17px;
}

.nav-tabs > .active > a, .nav-tabs > .active > a:hover {
    -moz-border-bottom-colors: none;
    -moz-border-left-colors: none;
    -moz-border-right-colors: none;
    -moz-border-top-colors: none;
    border-color: #B2B2B2 #B2B2B2 rgba(0, 0, 0, 0);
    border-image: none;
    border-style: solid;
    border-width: 1px;
}

.nav-tabs > li > a, .nav-pills > li > a {
    padding-left: 6px;
    padding-right: 6px;
}

