#tab-academic-information table th, #tab-admission table th {
    width: 194px;
}
#tab-curriculum table th.semester {
    min-width: 100px;
}


table th {
    background-color: #F7F7F7;
}

.nav-tabs {
    border-bottom: 1px solid #B2B2B2;
}

a {
    color: #007BC0;
}

img.link_blank, img.link_pdf {
    padding-bottom: 3px;
}

.well p {
    margin: 0;
}

td > p {
    margin-bottom: 0;
}

.unit-content .contact {
    background: linear-gradient(to bottom, #C8C8C8 0%, #FFFFFF 99%) repeat scroll 0 0 rgba(0, 0, 0, 0);
    border-radius: 10px;
    padding: 1px;
}

.unit-content .contact table th {
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
    text-align: left;
    width: 130px;
}

.unit-content .contact > div {
    background: linear-gradient(to bottom, #F5F5F5 0%, #FFFFFF 99%) repeat scroll 0 0 rgba(0, 0, 0, 0);
    border-radius: 10px;
    padding: 8px 15px;
}


.contact table {width:50%;}

#tab-curriculum .table tr:first-child td {
    border-top: 1px solid #b2b2b2;
}


