// EXTENSIO CLASSES DE SISTEMA DE PLONE
// -------------------------



//taules de SISTEMA
.listing {
    &:extend(.table);
    &:extend(.table-condensed);
    &:extend(.table-striped);
    &:extend(.table-hover);
    &:extend(.table-bordered);
}



// EXTENSIO FORMS
// ----------------------
label {
  font-weight:bold;
  color: @genwebTitles;
}
.formHelp {
  &:extend(.s);
  &:extend(.muted);
  display:block;
  font-weight:normal;
}
.discreet {
    &:extend(.muted);
}
.visualClear {
    &:extend(.clearfix);
}
// botons

.standalone, input[value^="Cancel"], [type="submit"] {
  &:extend(.btn);
}

.context {
    &:extend(.btn);
    &:extend(.btn-primary);
}

//formularis
.selected {
    &:extend(.active);
}
.fieldRequired {font-size: 0;}
.fieldRequired:after {font-size: 20px !important;}
.fieldRequired,
.required {
	&:after {
		color: @red;
		content: "*";
		top:.35em;
		position: relative;
		font-size:150%;
		line-height: 0;
	}
}
.selected.required:after {color:@white;}
.field {
	margin-bottom: @line-height-base;
	& input[type="radio"],
	input[type="checkbox"] {
		margin:0 0 3px;
	}
}

#archetypes-fieldname-title {
	& label {
        &:extend(.xxxl);
	}
	& input {
        &:extend(.xxxl);
        &:extend(.input-xxlarge);
		height:27px;
	}
	& .required:after {


	}
}

.formControls, #actionsView {
	padding-top: 25px;
	.box-shadow(0 -15px 15px -15px rgba(0,0,0,.25));
}

.searchButton {&:extend(.btn);}

div.label, label *.label {
	background:@white;
	text-shadow:none;
	font-size: 100%;
	color: @genwebTitles;
	white-space: normal;
}
input#title {font-weight:bold}

.template-personal-information [for="form.portrait"] ~ img {
	float:right;
    margin-bottom: 5px;
    &:extend(.img-polaroid);
}

// MENUS INTERNS
.contentViews {
    &:extend(.nav);
    &:extend(.nav-tabs);
	& .selected, .selected:hover {
        &:extend(.active);
	}
}
#portlet-prefs {
	border:0;
	.portletHeader {
		display:none;
	}
	& dd {
		margin:0;
	}
}
.configlets {
    &:extend(.nav);
    &:extend(.nav-tabs);
    &:extend(.nav-stacked);
	& img {float:right;}
}
.contentActions {line-height: 0} //per si plone es deixa un espai



// PORTAL MESSAGE
// -----------------------
.portalMessage {
    &:extend(.alert);
	&.info {&:extend(.alert-info);}
	//&.warning  {@extend .alert-warning;}
	&.error {&:extend(.alert-error);}
	& dt, & dd {margin:0; display:inline;}
}

// PAGINATION
// -----------------------
.listingBar {&:extend(.pagination);}
.listingBar li:only-child{ display:none !important;}

