// SITEMAP
// -----------------------
#portal-sitemap {
    &:extend(.unstyled);
	& > li {
		font-weight: bold;
		font-size:normal;
		margin-top:30px;
        &:extend(.xxxl);
		& > div {
			padding-bottom:10px;
			border-bottom:1px solid @table-border-color;
		}
		& a {
			color: @blue;
			&:after{
				content:" ";
				position:relative;
				width:14px;
				height:14px;
				display:inline-block;
				background:url("/++gw++static/images/gh.png") -239px -143px no-repeat;
				margin-left:5px;
			}
		}
	}
	& ul {
		.content-columns(3, 2em);
		-webkit-column-fill: auto;
		-moz-column-fill: auto;
		column-fill: auto;
		margin:0;
		& > li {
            &:extend(.xxs);
			font-weight: bold;
			font-size:normal;
			list-style: none;
			margin:0 0 5px;
			padding:5px 0;
			-webkit-column-break-inside: avoid;
			-moz-column-break-inside: avoid;
			column-break-inside: avoid;
			& > div {
				padding-bottom:0;
				padding-top:5px;
			}
			& a {
				color: darken(@blue,15%);
				&:after {
					content:none;
				}
			}
		}
		& ul {
			-webkit-column-count: 1;
			-moz-column-count: 1;
			column-count: 1;
		}
		& > li li {
			padding:5px 9px;
			line-height:12px;
			font-weight: normal;
			font-size:smaller;


			& > div {
				padding-top:0;
			}
			& a {
				color: @gray;
			}
		}
	}
}
