// calenendar-system.css
/* The main calendar widget.  DIV containing a table. */

.calendar { display: none; margin:0 auto; position:fixed !important; width:100% !important; height:100% !important; margin-top: 50%;}
.calendar table { background: #f2f2f2; border-radius:5px; width:222px; box-shadow: 0 0 100px black; top:50%; margin-top:-100px !important; margin: 0 auto; }
.calendar .button { text-align: center; background: #f2f2f2;}
.calendar .nav { background: #f2f2f2 url(menuarrow.gif) no-repeat 100% 100%;}
.calendar thead .title { font-weight: bold; text-align: center;}
.calendar thead .name { border-bottom: 1px solid #b2b2b2; border-top: 1px solid #b2b2b2; text-align: center; background: #f7f7f7;}
.calendar thead .weekend { color: #c00;}
.calendar thead .hilite { background: #cef;}
.calendar thead .active { box-shadow: inset 0 0 3px #08c;}
.calendar tbody {background: #fff;}
.calendar tbody .day {width: 2em; text-align: center; padding: 2px 0px;}
.calendar tbody .day.othermonth {font-size: 80%; color: #aaa;}
.calendar tbody .day.othermonth.oweekend { color: #faa;}
.calendar table .wn { border-right: 1px solid #b2b2b2; background: #f7f7f7;}
.calendar tbody .rowhilite td { background: #cef;}
.calendar tbody td.hilite { background: #08c; color: white;}
.calendar tbody td.active { box-shadow: inset 0 0 3px #08c;}
.calendar tbody td.selected { font-weight: bold; background: #08c; color: white;}
.calendar tbody td.weekend { color: #c00;}
.calendar tbody td.today { font-weight: bold; color: white; border-radius: 3px;}
.calendar tbody td.disabled { color: gray; }
.calendar tbody .emptycell { }
.calendar tbody .emptyrow { display: none; }

.calendar tfoot .ttip { background: #f7f7f7; border-top: 1px solid #b2b2b2; text-align: center;}
.calendar tfoot .hilite { background-color: #cef !important;}
.calendar tfoot .active { background-color: #08c !important;}

.calendar .combo {
  position: absolute;
  display: none;
  width: 4em;
  top: 0px;
  left: 0px;
  cursor: default;
  border: 1px solid;
  border-color: ButtonHighlight ButtonShadow ButtonShadow ButtonHighlight;
  background: Menu;
  color: MenuText;
  font-size: 90%;
  padding: 1px;
  z-index: 100;
}

.calendar .combo .label,
.calendar .combo .label-IEfix {
  text-align: center;
  display: block;
  padding: 1px;
}

.calendar .combo .label-IEfix {
  width: 4em;
}

.calendar .combo .active {
  padding: 0px;
  border: 1px solid #000;
}

.calendar .combo .hilite {
  background: Highlight;
  color: HighlightText;
}

.calendar td.time {
  border-top: 1px solid ButtonShadow;
  padding: 1px 0px;
  text-align: center;
  background-color: ButtonFace;
}

.calendar td.time .hour,
.calendar td.time .minute,
.calendar td.time .ampm {
  padding: 0px 3px 0px 4px;
  border: 1px solid #889;
  font-weight: bold;
  background-color: Menu;
}

.calendar td.time .ampm {
  text-align: center;
}

.calendar td.time .colon {
  padding: 0px 2px 0px 3px;
  font-weight: bold;
}

.calendar td.time span.hilite {
  border-color: #000;
  background-color: Highlight;
  color: HighlightText;
}

.calendar td.time span.active {
  border-color: #f00;
  background-color: #000;
  color: #080;
}

